import React from "react";
import { FormGroup, Input, Label } from "reactstrap";

export default function RadioInput({
  legend,
  name,
  options,
  onChange = () => {},
  ...props
}) {
  return (
    <FormGroup tag="fieldset">
      {legend ? <legend>{legend}</legend> : ""}

      {options.map(({ value, label, ...rest }) => (
        <FormGroup check>
          <Label check>
            <Input
              type="radio"
              name={name}
              value={value}
              onChange={onChange}
              {...rest}
              {...props}
            />
            {label}
          </Label>
        </FormGroup>
      ))}
    </FormGroup>
  );
}
