import React from 'react';
import Toaster from "components/global-components/Components/Toaster";
import { getIn } from "formik";
import filter from "lodash/filter";
import isNil from "lodash/isNil";
import map from "lodash/map";
import size from "lodash/size";
import { useState } from "react";
import Dropzone from "react-dropzone";
import { Link } from "react-router-dom";
import { FormFeedback, FormGroup } from "reactstrap";
import styled from "styled-components";

const getColor = (props) => {
  if (props.isDragAccept) {
    return "#00e676";
  }
  if (props.isDragReject) {
    return "#ff1744";
  }
  if (props.isDragActive) {
    return "#2196f3";
  }
  return "#eeeeee";
};
const Container = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  border-width: 3px;
  border-radius: 2px;
  border-color: ${(props) => getColor(props)};
  border-style: dashed;
  background-color: #fafafa;
  color: #bdbdbd;
  font-size: 18px;
  outline: none;
  transition: border 0.24s ease-in-out;
`;

const thumbsContainer = {
  display: "flex",
  flexDirection: "row",
  flexWrap: "wrap",
  marginTop: 16,
};

const thumb = {
  display: "inline-flex",
  borderradius: 2,
  border: "1px solid #eaeaea",
  marginBottom: 8,
  marginRight: 8,
  width: 100,
  height: 100,
  padding: 4,
  boxSizing: "border-box",
};

const thumbInner = {
  display: "flex",
  minWidth: 0,
  overflow: "hidden",
};

const FormDropZone = ({
  field,
  form: { touched, errors, setFieldValue },
  required,
  className,
  onDrop,
  onCancel,
  isMultiple = false,
  ...props
}) => {
  const invalid = getIn(touched, field.name) && getIn(errors, field.name);
  const [Images, setImages] = useState(field.value);

  const DeleteImageHandler = (e, fileName) => {
    e.preventDefault();
    const NonDeletedItem = filter(Images, ({ name }) => name !== fileName);
    setImages([...NonDeletedItem]);
    setFieldValue(field.name, NonDeletedItem);

    console.log("field value is : ", field.value);
  };

  const maxSize = 5242880;

  return (
    <div className="container border p-3">
      <Container>
        {" "}
        <FormGroup className="m-0">
          <Dropzone
            accept="image/jpeg, image/png,image/jpg"
            minSize={0}
            maxSize={maxSize}
            multiple={isMultiple}
            onDrop={(files) => {
              let newFiles;
              const { name } = field;

              newFiles = map(files, (file) =>
                Object.assign(file, {
                  preview: URL.createObjectURL(file),
                })
              );

              if (isMultiple === false) {
                setImages([...newFiles]);
                setFieldValue(name, [...newFiles]);
              } else {
                if (size(Images) + size(newFiles) > 10) {
                  Toaster("Images should not be more than 10 ", "error");
                } else {
                  setImages([...Images, ...newFiles]);
                  setFieldValue(name, [...field.value, ...newFiles]);
                }
              }
            }}
            onFileDialogCancel={(files) => {}}
            {...props}
          >
            {({ getRootProps, getInputProps }) => (
              <div {...getRootProps()}>
                <input {...getInputProps()} />
                <div className="dropzone-content drop-container">
                  <p>Drag 'n' drop some files here, or click to select files</p>
                </div>
              </div>
            )}
          </Dropzone>

          {invalid && (
            <FormFeedback className="mt-0 mb-1">
              {getIn(errors, field.name)}
            </FormFeedback>
          )}
        </FormGroup>
      </Container>

      <aside style={thumbsContainer}>
        {Images &&
          map(Images, ({ preview, location, name }) => {
            return (
              <div style={thumb} key={name}>
                <div style={thumbInner} className="img-container" alt={name}>
                  <img src={!isNil(location) ? location : preview} alt={location} />
                  <Link
                    className="img-delete"
                    onClick={(e) => DeleteImageHandler(e, name)}
                  >
                    <i class="far text-dark fa-trash-alt"></i>
                  </Link>
                </div>
              </div>
            );
          })}
      </aside>
    </div>
  );
};

export default FormDropZone;

// export default DropZoneField;
