import { getIn } from "formik";
import React from "react";
import { FormFeedback, FormGroup, Input, Label } from "reactstrap";
import map from "lodash/map";
import styled from "styled-components";
import includes from "lodash/includes";
import filter from "lodash/filter";

const GroupBox = styled.div`
  height: 150px;
`;

const GroupLabeldCheckBox = ({
  options,
  field,
  form: { touched, errors, setFieldValue },
  onChange = () => {},
  ...props
}) => {
  const invalid = getIn(touched, field.name) && getIn(errors, field.name);

  return (
    <GroupBox>
      {map(options, ({ label }) => {
        let isSelected = includes(field.value, label);

        return (
          <FormGroup check {...props}>
            <Label check for={label}>
              <Input
                type="checkbox"
                invalid={invalid}
                checked={isSelected}
                // defaultValue={false}
                onChange={(val) => {
                  let values = field.value;
                  if (val.target.checked) {
                    values = [...values, label];
                  } else {
                    values = filter(values, (item) => item !== label);
                  }

                  setFieldValue(field.name, values);
                  onChange();
                }}
                // defaultChecked={field.value}
              />
              {label}
            </Label>
            {invalid && (
              <FormFeedback className="d-block mt-0 mb-1">
                {getIn(errors, field.name)}
              </FormFeedback>
            )}
          </FormGroup>
        );
      })}
    </GroupBox>
  );
};

export default GroupLabeldCheckBox;
