import React from "react";
import styled from "styled-components";
import size from "lodash/size";
import first from "lodash/first";
import upperCase from "lodash/upperCase";

const InitialWrapper = styled.div`
  width: 35px;
  height: 35px;
  justify-content: center;
  align-items: center;
  display: flex;
`;

const UserMenuWrapper = styled.li`
  .sub-menu {
    left: -155px !important;
  }
`;

export const UserMenu = ({ children, ...rest }) => (
  <UserMenuWrapper {...rest}>{children}</UserMenuWrapper>
);

const Initial = ({ name }) => {
  let initials = "U";

  if (size(name) > 0) {
    initials = upperCase(first(first(name)));
  }
  return (
    <InitialWrapper className="bg-white font-weight-bold h4 pl-2 pr-2 rounded-circle text-primary">
      {initials}
    </InitialWrapper>
  );
};
export default Initial;
