import React from "react";
import { Input, Label, FormGroup } from "reactstrap";
import ToolTipInfo from "./ToolTipInfo";
import { isNil } from "lodash";

const TextInput = ({
  label,
  type,
  placeholder,
  disabled,
  id,
  onChange,
  ...props
}) => {
  return (
    <FormGroup>
      {!isNil(label) && <Label for={label}>{label}</Label>}
      {props.children ? (
        <ToolTipInfo id={id}>{props.children}</ToolTipInfo>
      ) : (
        ""
      )}
      <Input
        type={type}
        placeholder={placeholder}
        value={props.value}
        {...props}
        disabled={disabled}
        onChange={(e) => onChange(e)}
      />
    </FormGroup>
  );
};

TextInput.defaultProps = {
  disabled: false,
};

export default TextInput;
