import React from 'react';
import { useEffect, useState } from "react";
import SweetAlert from "sweetalert-react";
//import Icon from "./information.svg";
const ConfirmationFlow = ({
  show,
  type,
  title,
  text,
  onConfirm,
  onCancel,
  isConfirmed,
  onClose,
  confirmButtonText,
  ...props
}) => {
  const [isShow, setIsShow] = useState(show || false);
  const [alertType, setAlertType] = useState(type);
  const [alertTitle, setAlertTitle] = useState(title || "warning");
  const [alertText, setAlertText] = useState(
    text || "This action is not reversible"
  );

  useEffect(() => {
    if (isConfirmed) {
      setAlertType("success");
      setAlertTitle("");
      setAlertText("");

      setTimeout(() => {
        if (isConfirmed === true) {
          setAlertType(alertType);
          setAlertTitle(alertTitle);
          setAlertText(alertText);
          onClose();
        }
      }, 1000);
    } else {
      setIsShow(show);
    }
  }, [isConfirmed, show]);

  return (
    <SweetAlert
      showCancelButton
      showLoaderOnConfirm
      show={isShow}
      icon={"success"}
      text={alertText}
      type={alertType}
      // imageUrl={alertType === "question" ? Icon : ""}
      title={alertTitle}
      onConfirm={onConfirm}
      onCancel={onCancel}
      dangerMode={true}
      confirmButtonText={confirmButtonText}
      {...props}
    />
  );
};

export default ConfirmationFlow;
