import React from "react";
import { Input, Label, FormGroup } from "reactstrap";

const DropDown = ({ options, label, bsSize, onChange, props }) => {
  if (onChange) {
    return (
      <FormGroup>
        {label && <Label for={label}>{label}</Label>}
        <Input type="select" bsSize={bsSize} {...props} onChange={onChange}>
          {options.map((obj) => (
            <option value={obj}>{obj}</option>
          ))}
        </Input>
      </FormGroup>
    );
  } else {
    return (
      <FormGroup>
        {label && <Label for={label}>{label}</Label>}
        <Input type="select" bsSize={bsSize} {...props}>
          {options.map((obj) => (
            <option>{obj}</option>
          ))}
        </Input>
      </FormGroup>
    );
  }
};

export default DropDown;
