import React from "react";
import Switch from "react-switch";
import { FormGroup, Label } from "reactstrap";
import styled from "styled-components";
import cx from "classnames";

const StyledLabel = styled(Label)`
  ${({ bold = false }) =>
    bold &&
    ` font-size: 14px;
font-weight: 700;
color: rgb(44, 44, 44);`}
`;

const SwitchToggler = ({
  label,
  className,
  checked = false,
  noMargin = false,
  onChange = () => {},
  ...rest
}) => {
  return (
    <FormGroup className={cx("d-flex flex-column", { "mb-0": noMargin })}>
      <StyledLabel
        {...rest}
        for={rest.for || label}
        className="m-0 mr-1"
        align="center"
      >
        {label}
      </StyledLabel>

      <Switch
        checked={checked}
        onChange={onChange}
        className={className}
        {...rest}
      />
      {/* {touched && error && <div className="error">{error}</div>} */}
    </FormGroup>
  );
};

export default SwitchToggler;
