import { getIn } from "formik";
import React from "react";
import { FormFeedback, FormGroup, Input, Label } from "reactstrap";
import ToolTipInfo from "./ToolTipInfo";
import isNil from "lodash/isNil";
import cx from "classnames";

const ReactStrapFormGroupInput = ({
  label,
  field,
  form: { touched, errors },
  type,
  labelClass,
  placeholder,
  className = "",
  bsSize = "md",
  disabled,
  id,
  onChange,
  required,
  ...props
}) => {
  const invalid = getIn(touched, field.name) && getIn(errors, field.name);

  return (
    <FormGroup className="mb-1">
      {label !== undefined && (
        <Label
          htmlFor={label}
          className={cx({ requiredField: required }, labelClass)}
          for={props.for || props.label}
        >
          {label}
        </Label>
      )}
      {props.children ? (
        <ToolTipInfo id={id}>{props.children}</ToolTipInfo>
      ) : (
        ""
      )}
      <Input
        type={type}
        placeholder={placeholder}
        {...props}
        {...field}
        size={bsSize}
        disabled={disabled}
        className={`${className}`}
        onKeyPress={props.onKeyPress}
        invalid={!isNil(invalid)}
      />
      {invalid && (
        <FormFeedback className="mt-0 mb-1 w-90">
          {getIn(errors, field.name)}
        </FormFeedback>
      )}
    </FormGroup>
  );
};

ReactStrapFormGroupInput.defaultProps = {
  disabled: false,
};

export default ReactStrapFormGroupInput;
