import React from 'react';
import Footer from "components/global-components/footer-v2";
import Navbar from "components/global-components/navbar";
import PageHeader from "components/global-components/page-header";
import "loaders.css/src/animations/ball-beat.scss";
import BlockUi from "react-block-ui";
import "react-block-ui/style.css";
import { Loader } from "react-loaders";

const Base = ({
  children,
  Header,
  hideSearch,
  isHideFooter = false,
  noBanner,
  noHeader,
  pageName,
  isHideNavigation = false,
  isLoading,
  blockingMessage = "",
  getIndex,
  setSearch,
  footer = {},
}) => {
  return (
    <BlockUi
      tag="div"
      blocking={isLoading}
      className="block-overlay-dark"
      loader={
        <Loader
          style={{ transform: "scale(1.5)" }}
          color="#2e6ca0"
          active
          type="ball-beat"
        />
      }
    >
      {!isHideNavigation && <Navbar noBanner={noBanner} isHeaderFixed={true} />}

      {!noHeader && (
        <PageHeader
          pageName={pageName}
          getIndex={getIndex}
          setSearch={setSearch}
          hideSearch={hideSearch}
          headertitle={Header.headertitle}
          subheader={Header.subheader}
        />
      )}

      {children}
      {!isHideFooter && <Footer {...footer} />}
    </BlockUi>
  );
};

export default Base;
