import React from 'react';
import { getIn } from "formik";
import filter from "lodash/filter";
import includes from "lodash/includes";
import map from "lodash/map";
import { Card } from "reactstrap";

import cx from "classnames";
import styled from "styled-components";

const AminitiesLink = styled.div`
  :hover {
    .card {
      background-color: #1a5282 !important;
      i {
        color: white;
      }
    }
  }
`;

const GroupBox = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
`;

const GroupCheckBox = ({
  options,
  field,
  form: { touched, errors, setFieldValue },
  ...props
}) => {
  const { type, text, icon } = props;
  // const [isSelected, setIsSelected] = useState(field.value);
  //   const toggleCheckBox = (e) => {
  //     e.preventDefault();
  //     setFieldValue(field.name, !isSelected);
  //     setIsSelected(!isSelected);
  //   };
  const invalid = getIn(touched, field.name) && getIn(errors, field.name);

  return (
    <GroupBox>
      {map(options, ({ text, icon }, index) => {
        let isSelected = includes(field.value, text);
        return (
          <AminitiesLink
            key={index}
            onClick={(e) => {
              let values = field.value;

              if (isSelected) {
                values = filter(values, (item) => item !== text);
                // setIsSelected(false);
              } else {
                values = [...values, text];
                // setIsSelected(true);
              }
              setFieldValue(field.name, values);
            }}
            className="text-center aminitiess float-left ml-md-3 mb-3"
          >
            <Card
              className={cx("align-items-center  justify-content-center ", {
                blueBg: isSelected,
                whiteBg: !isSelected,
              })}
            >
              <i
                className={cx(icon, {
                  whiteIcon: isSelected,
                  grayfont: !isSelected,
                })}
              ></i>
            </Card>
            {type === "textInside" && (
              <div
                className={cx("aminitiesText", {
                  whitefont: isSelected,
                  grayfont: !isSelected,
                })}
              >
                {text}
              </div>
            )}
          </AminitiesLink>
        );
      })}
    </GroupBox>
  );
};
export default GroupCheckBox;
