import API from "../../lib/api/Listings";
import BaseEpic from "./../BaseEpic";
import {
  MYListBegin,
  MYListFailure,
  MYListSuccess,
  UpdateBegin,
  UpdateFailure,
  UpdateSuccess,
  Begin,
  Success,
  Failure,
  ListSuccess,
  ListBegin,
  ListFailure,
  SaveSearch,
} from "./Actions";
import isNil from "lodash/isNil";

const baseEpicObject = new BaseEpic();
const BASE = "LISTING";
const BASE_MY_LISTING = "MY_LISTING";

const { search, create, update } = baseEpicObject.CreateModuleEpics(BASE, API);

const { removeSelected } = baseEpicObject.CreateModuleEpics(
  BASE_MY_LISTING,
  API
);

const get = (params) => async (dispatch) =>
  baseEpicObject.makeCall(
    API.get,
    { id: params },
    Begin,
    Success,
    Failure,
    dispatch
  );

const getList = (params) => async (dispatch) =>
  baseEpicObject.makeCall(
    API.getPublishedList,
    { params },
    ListBegin,
    ListSuccess,
    ListFailure,
    dispatch
  );

const getMyList = (params) => async (dispatch) =>
  baseEpicObject.makeCall(
    API.getMyListing,
    { params },
    MYListBegin,
    MYListSuccess,
    MYListFailure,
    dispatch
  );

const updateImages = (id, params) => async (dispatch) =>
  baseEpicObject.makeCall(
    API.updateWithFormData,
    { id, params },
    UpdateBegin,
    UpdateSuccess,
    UpdateFailure,
    dispatch
  );

const saveSearch = (params) => async (dispatch) => {
  await dispatch(SaveSearch({ data: params }));
};

const setFormEmpty = (callback) => async (dispatch) => {
  await dispatch(Success({ data: {} }));
  !isNil(callback) && callback();
};

export {
  get,
  removeSelected,
  getList,
  search,
  create,
  update,
  getMyList,
  updateImages,
  setFormEmpty,
  saveSearch,
};
