import cx from "classnames";
import { getIn } from "formik";
import differenceBy from "lodash/differenceBy";
import isNil from "lodash/isNil";
import React from "react";
import Select from "react-select";
import { FormGroup, Label } from "reactstrap";

const customStyles = {
  menuPortal: (provided) => ({ ...provided, zIndex: 2000 }),

  control: (provided, state) => ({
    ...provided,
    minHeight: "31px",
    height: "31px",
    boxShadow: state.isFocused ? null : null,
  }),

  valueContainer: (provided, state) => ({
    ...provided,
    height: "31px",
    padding: "0 6px",
  }),

  input: (provided, state) => ({
    ...provided,
    margin: "0px",
  }),

  indicatorsContainer: (provided, state) => ({
    ...provided,
    height: "31px",
  }),
};

const customStylesWithError = {
  control: (provided, state) => ({
    ...provided,
    borderColor: "red",
  }),
};

const ClearIndicatorStyles = (base, state) => ({
  ...base,
  cursor: "pointer",
  color: state.isFocused ? "blue" : "black",
});
const getSelectedValue = (options, field) => {
  return options.find((option) => option.value === field.value);
};

const ReactSelect = ({
  options,
  field,
  form,
  isMulti,
  required,
  onClose,
  reactSelectSize = "md",
  selectedValue = "",
  onChange,
  exclude = [],
  ...props
}) => {
  let invalid = form.touched && getIn(form.errors, field.name);
  let styles = reactSelectSize === "sm" ? customStyles : {};

  styles = !isNil(invalid) ? { ...styles, ...customStylesWithError } : styles;
  // todo: useEffect to achieve this
  if (onClose !== undefined && options !== undefined) {
    onClose(options.find((option) => option.value === field.value));
  }

  const getOptions = () => differenceBy(options, exclude, "value");

  return (
    <FormGroup>
      {props.label && (
        <Label
          for={props.for || props.label}
          className={cx(props.className, cx({ requiredField: required }))}
        >
          {props.label}
        </Label>
      )}
      <Select
        classNamePrefix="sis-react-select"
        styles={{ ...styles, clearIndicator: ClearIndicatorStyles }}
        className={[
          cx(
            { "invalid-dd": invalid },
            { "styled-element-disabled": props.isDisabled === true }
          ),
        ]}
        height={20}
        isMulti={isMulti}
        options={getOptions()}
        name={field.name}
        value={getSelectedValue(options, field)}
        onChange={(option) => {
          const { name } = field;
          const { value = "" } = option ? option : "";
          const { setFieldValue } = form;

          setFieldValue(name, value);
          // notify to component
          if (onClose) onClose(option && option); // todo: change onClose Params to general and handle in called component
          if (onChange) onChange({ ...option, name, value });
        }}
        onBlur={field.onBlur}
        placeholder={props.placeholder}
        {...props}
        isClearable
        menuPlacement="auto"
      />
      {invalid && <div className="invalid-feedback d-block m-0">{invalid}</div>}
    </FormGroup>
  );
};

export default ReactSelect;
