export const getIdMode = ({ match: { params: { id } = {} } }) => {
  return typeof id === "string" && id === "create" ? null : id;
};

export const getMode = (props) => {
  const idAsMode = getIdMode(props);
  if (idAsMode) {
    return {
      mode: "edit",
    };
  } else {
    return {
      mode: "create",
    };
  }
};
