import LoadingButton from "components/global-components/Components/Buttons/LoadingButton";
import { Formik, useFormikContext } from "formik";
import isEmpty from "lodash/isEmpty";
import React from "react";
import { Prompt } from "react-router-dom";
import { Form } from "reactstrap";

const PromptIfDirty = () => {
  const formik = useFormikContext();

  const { dirty, submitCount, touched } = formik;

  return <Prompt message="hello world" when={dirty && submitCount === 0 && !isEmpty(touched)} />;
};

const PromptIfDirtyWithMessage = PromptIfDirty;

const FormikHOC = (WrappedComponent) => {
  class BaseFormikWrapper extends React.PureComponent {
    constructor(props) {
      super(props);
      this.state = {};
    }
    render() {
      const {
        initialValues,
        onSubmit,
        validationSchema,
        submitButtonLabel,
        BtnClasses,
        isSubmitting,
        isFormikSubmit = true,
        ...rest
      } = this.props;

      return (
        <Formik
          initialValues={initialValues}
          validationSchema={validationSchema}
          onSubmit={(values, errors) => {
            onSubmit(values, { rest });
          }}
          validateOnChange={false}
          validateOnBlur={true}
          {...rest}
        >
          {({ isValid, ...props }) => (
            <Form onSubmit={props.handleSubmit}>
              <PromptIfDirtyWithMessage />
              <WrappedComponent {...rest} {...props} />

              {isFormikSubmit && (
                <LoadingButton
                  label={submitButtonLabel}
                  isSubmitting={isSubmitting}
                  onClick={props.handleSubmit}
                  className={BtnClasses}
                />
              )}
            </Form>
          )}
        </Formik>
      );
    }
  }
  return BaseFormikWrapper;
};

export default FormikHOC;
