import { getIn } from "formik";
import isNil from "lodash/isNil";
import React from "react";
import { FormFeedback, Input, Label, FormGroup } from "reactstrap";
import ToolTipInfo from "./ToolTipInfo";
import cx from "classnames";

const TextInputwithaddon = ({
  label,
  field,
  form: { touched, errors },
  type,
  placeholder,
  disabled,
  addon,
  position,
  datavalmsgfor,
  tooltext,
  customButton = "",
  required,
  onChange,
  ...props
}) => {
  const invalid = getIn(touched, field.name) && getIn(errors, field.name);

  if (onChange !== undefined) {
    return (
      <FormGroup>
        <Label
          className={cx({ requiredField: required })}
          for={props.for || props.label}
        >
          {label}
        </Label>
        {props.children ? <ToolTipInfo>{props.children}</ToolTipInfo> : ""}
        <div className="input-group">
          {position === "left" && !isNil(addon) ? (
            <div className="input-group-prepend">
              <span className="input-group-text">{addon}</span>
            </div>
          ) : (
            ""
          )}
          <Input
            type={type}
            placeholder={placeholder}
            {...props}
            {...field}
            // invalid={!isNil(invalid)}
            disabled={disabled}
            onChange={onChange}
          />
          <span
            className="field-validation-valid"
            data-valmsg-for={datavalmsgfor}
            data-valmsg-replace="true"
          ></span>
          {position === "right" ? (
            <div className="input-group-addon">{addon}</div>
          ) : (
            ""
          )}
        </div>
        {invalid && (
          <FormFeedback className="mt-0 mb-1">
            {getIn(errors, field.name)}
          </FormFeedback>
        )}
      </FormGroup>
    );
  } else {
    return (
      <FormGroup>
        <Label
          className={cx({ requiredField: required })}
          for={props.for || props.label}
        >
          {label}
        </Label>
        {props.children ? (
          <ToolTipInfo id={field.name}>{props.children}</ToolTipInfo>
        ) : (
          ""
        )}
        <div className="input-group">
          {position === "left" && !isNil(addon) ? (
            <div className="input-group-prepend">
              <span className="input-group-text">{addon}</span>
            </div>
          ) : (
            ""
          )}
          <Input
            type={type}
            placeholder={placeholder}
            name={field.name}
            {...props}
            {...field}
            invalid={!isNil(invalid)}
            disabled={disabled}
          />
          {customButton}
          <span
            className="field-validation-valid"
            data-valmsg-for={datavalmsgfor}
            data-valmsg-replace="true"
          ></span>
          {position === "right" ? (
            <div className="input-group-addon">{addon}</div>
          ) : (
            ""
          )}
        </div>
        {invalid && (
          <FormFeedback className="mt-0 mb-1 d-block">
            {getIn(errors, field.name)}
          </FormFeedback>
        )}
      </FormGroup>
    );
  }
};

export default TextInputwithaddon;
