import React, { useState } from "react";
import { Link } from "react-router-dom";
import { Card } from "reactstrap";
import { getIn } from "formik";

import cx from "classnames";
import styled from "styled-components";

const AminitiesLink = styled(Link)`
  :hover {
    .card {
      background-color: #1a5282 !important;
      i {
        color: white;
      }
    }
  }
`;
const CIconCheckBox = ({
  field,
  form: { touched, errors, setFieldValue },
  ...props
}) => {
  const { type, text, icon } = props;
  const [isSelected, setIsSelected] = useState(field.value);
  const toggleCheckBox = (e) => {
    e.preventDefault();
    setFieldValue(field.name, !isSelected);
    setIsSelected(!isSelected);
  };
  const invalid = getIn(touched, field.name) && getIn(errors, field.name);

  return (
    <AminitiesLink
      onClick={(e) => toggleCheckBox(e)}
      className="text-center aminitiess"
    >
      <Card
        className={cx("align-items-center  justify-content-center ", {
          blueBg: isSelected,
          whiteBg: !isSelected,
        })}
      >
        <i
          className={cx(icon, {
            whiteIcon: isSelected,
            grayfont: !isSelected,
          })}
        ></i>
      </Card>
      {type === "textInside" && (
        <div
          className={cx("aminitiesText", {
            whitefont: isSelected,
            grayfont: !isSelected,
          })}
        >
          {text}
        </div>
      )}
    </AminitiesLink>
  );
};
export default CIconCheckBox;
