// page level imports
import PageLayout from "components/PageLayouts";
import ConfirmationFlow from "components/global-components/Components/SweetAlert/ConfirmationFlow";
import { handleResponseWithToaster } from "components/global-components/Components/Toaster";
import isEmpty from "lodash/isEmpty";
import isNil from "lodash/isNil";
import isNull from "lodash/isNull";
import React from "react";
import { getIdMode, getMode } from "./helpers";

const BaseIndexPage = (WrappedComponent) => {
  class BasePage extends React.PureComponent {
    constructor(props) {
      super(props);

      this.state = {
        isLoading: false,
        isSelectAll: false,
        isShowConfirmation: false,
        isConfirmed: false,
        isCollapse: true,
        isSubmitting: false,
        isNavigateOnCreate: true,
        isNavigateOnUpdate: true,
        isHidePaging: true,
        isSinglePage: false,
        isShowToaster: true,
      };
    }

    turnOnLoading = (params) => {
      this.setState((state) => ({
        ...state,
        ...params,
        isLoading: true,
      }));
    };

    turnOffLoading = (params) => {
      this.setState((state) => ({
        ...state,
        ...params,
        isLoading: false,
      }));
    };

    turnOnSubmitting = () => {
      this.setState((state) => ({
        ...state,
        isSubmitting: true,
      }));
    };

    turnOffSubmitting = () => {
      this.setState((state) => ({
        ...state,
        isSubmitting: false,
      }));
    };

    getItem = (id) => {
      this.turnOnLoading();
      this.props
        .get(id)
        .then(() => {
          this.turnOffLoading();
        })
        .catch((err) => {
          console.log(err);
          this.turnOffLoading();
        });
    };

    getIndex = (props) => {
      this.turnOnLoading();
      this.props.getList(props).then(() => {
        this.turnOffLoading({ isHidePaging: true });
      });
    };

    setSearch = (props) => {
      this.props.saveSearch(props).then(() => {});
    };

    onEditItem = (item, callback) => {
      const { _id } = item;
      this.turnOnLoading();
      this.props.get(_id).then((res) => {
        !isNil(callback) && callback(res);
        this.turnOffLoading();
      });
    };

    handleResponse = ({ response }) => {
      handleResponseWithToaster({
        isShowToaster: this.state.isShowToaster,
        response,
        onAfterSuccess: this.turnOffSubmitting(),
        onAfterFailure: this.turnOffSubmitting(),
      });
    };

    onSubmitItem = (params, callback) => {
      const { id } = params;
      this.turnOnSubmitting();

      if (!isEmpty(id) && !isNull(id)) {
        this.props.update(id, params).then((response) => {
          if (!isNil(callback)) {
            callback(response);
          } else {
            this.handleResponse({ response });
            this.handleAfterUpdateActions({ response });
            !isNil(callback) && this.turnOffSubmitting();
          }
        });
      } else {
        this.props.create(params).then((response) => {
          if (!isNil(response)) {
            if (!isNil(callback)) {
              callback(response);
            } else {
              this.handleResponse({ response });
              this.handleAfterCreateActions({ response });
              this.turnOffSubmitting();
            }
          }
        });
      }
    };

    navigateToActiveTabIndex = ({ id }) => {
      const {
        match: { path },
        location,
      } = this.props;
      this.navigateTo({
        ...location,
        pathname: `${path}/${id}`,
        state: {
          id,
        },
      });
    };

    handleAfterCreateActions = ({ response, activeTab }) => {
      if (this.state.isSinglePage) return;

      const { data: { _id: id } = {} } = response;
      if (this.state.isNavigateOnCreate && !isEmpty(id)) {
        this.navigateToActiveTabIndex({ id, activeTab });
      }
    };

    handleAfterUpdateActions = ({ response, activeTab }) => {
      if (this.state.isSinglePage) return;

      const { data: { _id: id } = {} } = response;
      if (this.state.isNavigateOnUpdate && !isEmpty(id)) {
        this.navigateToActiveTabIndex({ id, activeTab });
      }
    };

    onSubmitList = (id, params) => {
      this.turnOnSubmitting();
      this.props.update(id, params).then((response) => {
        this.handleResponse({ response });
      });
    };

    unSelectAllItems = () => {
      this.props.markSelected(false);
      this.setState({ isSelectAll: false });
    };

    navigateTo = (path) => {
      this.props.history.replace(path);
    };

    confirmThenRemove = ({ item }) => {
      this.props.markSelected({ ...item, selected: true });
      this.setIsShowConfirmation(true);
    };

    setIsShowConfirmation = (isShowConfirmation) => {
      this.setState((state) => ({ ...state, isShowConfirmation }));
    };

    onConfirm = () => {
      this.turnOnSubmitting();
      this.props
        .remove(this.props.selectedIds)
        .then((response) => {
          this.handleResponse({ response });
          this.setState((state) => ({
            ...state,
            isConfirmed: true,
          }));
        })
        .catch((err) => {
          this.setState((state) => ({
            ...state,
            isConfirmed: true,
          }));
        });
    };

    onConfirmClosed = () => {
      this.setState((state) => ({
        ...state,
        isShowConfirmation: false,
        isConfirmed: false,
      }));
      // this.unSelectAllItems();
    };

    render() {
      const {
        isLoading,
        isSubmitting,
        isShowConfirmation,
        isConfirmed,
      } = this.state;
      return (
        <PageLayout
          getIndex={this.getIndex}
          setSearch={this.setSearch}
          isLoading={isLoading}
          {...this.props}
        >
          <ConfirmationFlow
            show={isShowConfirmation}
            isConfirmed={isConfirmed}
            onConfirm={this.onConfirm}
            onClose={() => this.onConfirmClosed()}
            onCancel={() => {
              this.unSelectAllItems();
              this.setIsShowConfirmation(false);
            }}
            confirmButtonText="Delete"
            title="Warning"
            text="This action is not reversible"
            confirmButtonColor="#dc3545"
          />
          <WrappedComponent
            {...this.props}
            getMode={getMode}
            getIdMode={getIdMode}
            isLoading={isLoading}
            isSubmitting={isSubmitting}
            getIndex={this.getIndex}
            getItem={this.getItem}
            setSearch={this.setSearch}
            onEditItem={this.onEditItem}
            onSubmitItem={this.onSubmitItem}
            turnOnLoading={this.turnOnLoading}
            turnOffLoading={this.turnOffLoading}
            turnOffSubmitting={this.turnOffSubmitting}
            turnOnSubmitting={this.turnOnSubmitting}
            handleResponse={this.handleResponse}
            onDelete={this.confirmThenRemove}
          />
        </PageLayout>
      );
    }
  }

  return BasePage;
};

export default BaseIndexPage;
