import React, { useState } from "react";
import { Tooltip } from "reactstrap";
import Info from "./info.svg";

const ToolTipInfo = ({ id, ...props }) => {
  let imagealt = "image";
  const [tooltipOpen, setTooltipOpen] = useState(false);

  const toggle = () => setTooltipOpen(!tooltipOpen);

  return (
    <>
      <span className="tooltipbody" href="#" id={id}>
        {" "}
        {/* <img
          src={`${publicUrl}  
        /assets/img/info1.png `}
          alt={imagealt}
        ></img> */}
        <img src={Info} alt={imagealt} />
      </span>
      <div>
        <Tooltip
          placement="top"
          arrowClassName="tool-tiparrow"
          innerClassName="tool-tip"
          isOpen={tooltipOpen}
          autohide={false}
          target={id}
          toggle={toggle}
          hideArrow={true}
        >
          {props.children}
        </Tooltip>
      </div>
    </>
  );
};

export default ToolTipInfo;
