const mapStateToProps = (state, props) => ({
  noBanner: true,
  isHideFooter: true,
  noHeader: true,
  isHideNavigation: true,
  item: selectItem(state),
});

const mapDispatchToProps = (dispatch) => ({
  checkEmail: (params) => dispatch(checkEmail(params)),
});

export default connect(mapStateToProps, mapDispatchToProps)(SignInModalBody);
