import cx from "classnames";
import { getIn } from "formik";
import React from "react";
import { FormFeedback, FormGroup, Input, Label } from "reactstrap";
const ReactStrapTextArea = ({
  field,
  form: { touched, errors },
  required,
  className,
  initialRows,
  ...props
}) => {
  const invalid = getIn(touched, field.name) && getIn(errors, field.name);
  return (
    <FormGroup>
      {props.label && (
        <Label
          className={cx({ requiredField: required })}
          for={props.for || props.label}
        >
          {props.label}
        </Label>
      )}

      <Input
        type="textarea"
        rows={initialRows ? initialRows : 4}
        {...field}
        {...props}
        invalid={invalid}
        className={`${className} m-0`}
      />
       {invalid && (
        <FormFeedback className="mt-0 mb-1">
          {getIn(errors, field.name)}
        </FormFeedback>
      )}
    </FormGroup>
  );
};

export default ReactStrapTextArea;
