import React from "react";
import { Input, Label, FormGroup, FormFeedback } from "reactstrap";
import isNil from "lodash/isNil";
import { getIn } from "formik";

const ReactStrapNumberInput = ({
  label,
  field,
  form: { touched, errors },
  onChange,
  ...props
}) => {
  const invalid = getIn(touched, field.name) && getIn(errors, field.name);

  return (
    <FormGroup>
      {label && <Label for={label}>{label}</Label>}
      <Input
        type="number"
        {...props}
        {...field}
        // onChange={(e) => onChange(e)}
        invalid={!isNil(invalid)}
        onKeyDown={(e) =>
          (e.keyCode === 69 || e.keyCode === 190) && e.preventDefault()
        }
      ></Input>
      {invalid && <FormFeedback>{getIn(errors, field.name)}</FormFeedback>}
    </FormGroup>
  );
};

export default ReactStrapNumberInput;
