import React from 'react';
import LocationPicker from "components/global-components/Components/LocationPicker";
import { getIn } from "formik";
import { GoogleApiWrapper } from "google-maps-react";
import replace from "lodash/replace";
import { useState } from "react";
import PlacesAutocomplete, {
  geocodeByAddress,
  getLatLng,
} from "react-places-autocomplete";
import { FormFeedback } from "reactstrap";

const GooglePlacesInput = ({
  field,
  form: { touched, errors, setFieldValue },
}) => {
  const invalid = getIn(touched, field.name) && getIn(errors, field.name);

  const [address, setAddress] = useState("");
  const handleChange = (address) => {
    setAddress(address);

    setFieldValue(field.name, address);
  };

  const handleSelect = (address) => {
    geocodeByAddress(address)
      .then((results) => {
        let lat = getLatLng(results[0]);
        return lat;
      })
      .then((latLng) => {
        handleChange(address, latLng);
      })
      .catch((error) => console.error("Error", error));
  };

  return (
    <>
      <PlacesAutocomplete
        searchOptions={{
          componentRestrictions: { country: ["us"] },
          // location: new google.maps.LatLng(-34, 151),
          //radius: 2000,
          types: ["address"],
        }}
        className="d-flex w-100"
        value={address}
        onChange={handleChange}
        onSelect={handleSelect}
      >
        {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => {
          return (
            <>
              <input
                type="text"
                placeholder="Where do you want to live?"
                {...getInputProps({
                  placeholder: "Search Property",
                })}
                autoFocus
              />

              <div
                className="autocomplete-dropdown-container w-100 pl-2 text-left "
                style={{ position: "absolute", zIndex: 99999 }}
              >
                {loading && <div className="bg-white">Loading...</div>}
                {suggestions.map((suggestion, index) => {
                  const { description, active } = suggestion;
                  const style = suggestion.active
                    ? { backgroundColor: "#fafafa", cursor: "pointer" }
                    : { backgroundColor: "#ffffff", cursor: "pointer" };
                  return (
                    <div
                      key={index}
                      {...getSuggestionItemProps(suggestion, {
                        style,
                      })}
                    >
                      <span>
                        <LocationPicker />
                        {replace(description, ", USA", "")}
                      </span>
                    </div>
                  );
                })}
              </div>
            </>
          );
        }}
      </PlacesAutocomplete>
      {invalid && (
        <FormFeedback className="mt-0 mb-1 ">
          {getIn(errors, field.name)}
        </FormFeedback>
      )}
    </>
  );
};

export default GoogleApiWrapper({
  apiKey: process.env.REACT_APP_GOOGLE_MAP_API_KEY,
})(GooglePlacesInput);
