import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import { CKEditor } from "@ckeditor/ckeditor5-react";
import cx from "classnames";
import { getIn } from "formik";
import isNil from "lodash/isNil";
import React from "react";
import { FormFeedback, FormGroup, Input, Label } from "reactstrap";

const ReactStrapTextEditor = ({
  field,
  onChange,
  placeholder,
  form,
  required,
  name,
  multilingual = true,
  ...props
}) => {
  let invalid =
    getIn(form.touched, field.name) && getIn(form.errors, field.name);

  return (
    <FormGroup>
      {props.label && (
        <Label
          className={cx({ requiredField: required })}
          for={props.for || props.label}
        >
          {props.label}
        </Label>
      )}
      <CKEditor
        data={field.value}
        name={name}
        placeholder={placeholder}
        editor={ClassicEditor}
        onBlur={(event, editor) => {
          const data = editor.getData();
          form.setFieldValue(field.name, data);
        }}
        onChange={(event, editor) => {
          const data = editor.getData();

          onChange(data);
        }}
        invalid={!isNil(invalid)}
        {...props}
      />
      <Input {...field} {...props} type="hidden" invalid={!isNil(invalid)} />

      {invalid && (
        <FormFeedback className="mt-0 mb-1">
          {getIn(form.errors, field.name)}
        </FormFeedback>
      )}
    </FormGroup>
  );
};
export default ReactStrapTextEditor;
